/**
 * @name isObjectLike
 * @description Checks if the value is object like, which is not null and an object.
 * @param {val} any
 * @returns boolean
 * @since 1.0.0
 * @example
 * isObjectLike(null);
 * // => false
 * isObjectLike([]);
 * // => true
 * isObjectLike({});
 * // => true
 */
export var isObjectLike = function isObjectLike(value) {
  return null != value && "object" === typeof value;
};
export default isObjectLike;